import React from 'react';
import NavBar from './NavBar';
import '../Styles/About.css';
import { Link } from 'react-router-dom';

class About extends React.Component {

    constructor(props) {
      super(props)
      this.state = {
        skills: true
      }
      this.techSkills = this.techSkills.bind(this)
      this.appSkills = this.appSkills.bind(this)
    }

    componentDidMount() {
      document.title = "Ryan Jacobson | About"
    }

    techSkills() {
      this.setState({
        skills: true
      })
    }

    appSkills() {
      this.setState({
        skills: false
      })
    }

    render() {
      return(
        <div>
          <NavBar />
          <div className="about-container">
            <h1 className="about-title">ABOUT</h1> 
            <div className="about">
                  <p className="drop-caps">My name is Ryan Jacobson and I am a full stack web developer. My interest in 
                  programming has led me to many opportunities including an internship 
                  at J.P Morgan Chase, where I gained hands on experience in a corporate
                  environment. I have developed a passion for computer software and I look
                  forward to many more learning opportunities to come.
                  <br></br>
                  <span><Link className="about-button" to="/contact">Contact Me</Link></span></p>
              </div>
          </div>
          <div className="skills-title">
            <h2 className="tech" onClick={this.techSkills} style={{backgroundSize: this.state.skills ? '100% 30px' : '0% 30px', color: this.state.skills ? 'black' : '#f2f2f2'}}>Technical Skills</h2>
            <h2 className="app" onClick={this.appSkills} style={{backgroundSize: this.state.skills ? '0% 30px' : '100% 30px', color: this.state.skills ? '#f2f2f2' : 'black'}}>Application Skills</h2>
          </div>
            <div className="skills">
              <div className={this.state.skills ? 'show' : 'remove'}>
                <ul>
                  <li><i style={{color: "#ff1717"}} className="fab fa-adobe"></i>Adobe Creative Suite</li>
                  <li><i style={{color: "#17aaff"}} className="fab fa-microsoft"></i>Microsoft Office</li>
                </ul>
              </div>
              <div className={this.state.skills ? 'remove' : 'show'}>
                <ul>
                  <li><i style={{color: "#ff742e"}} className="fab fa-html5"></i>HTML</li>
                  <li><i style={{color: "#3aa2fc"}} className="fab fa-css3-alt"></i>CSS</li>
                  <li><i style={{color: "#fce93a"}} className="fab fa-js"></i>JavaScript</li>
                  <li><i style={{color: "#0071d4"}} className="fab fa-python"></i>Python</li>
                  <li><i style={{color: "#ff1717"}} className="fab fa-java"></i>Java</li>
                  <li><i style={{color: "#17aaff"}} className="fab fa-react"></i>React</li>
                </ul>
              </div>
            </div>
        </div>
      );
    }
  }
  
  export default About;


