import React from 'react';
import { Link } from 'react-router-dom';
import '../Styles/Home.css'
import Typewriter from 'typewriter-effect';
import NavBar from './NavBar.js'
import Background from './Background';

class Home extends React.Component {

    componentDidMount() {
      document.title = "Ryan Jacobson | Home"
    }

    render() {
      return (
        <div>
          <NavBar />
          <div className="container">
            <h1>Ryan.</h1>
            <div className="list"><Typewriter
              options={{
                strings: ['Full Stack Developer', 'UX Designer', 'Software Engineer', 'Student'],
                autoStart: true,
                loop: true,
              }}
            /></div>
            <Link className="view" to="/projects">View Projects</Link>
            <div className="background"><Background /></div>
          </div>
        </div>
      );
    }
  }
  
  export default Home;